import { MDXProvider } from "@mdx-js/react";
import React, { ReactElement, ReactNode } from "react";
import {
  Button,
  Text,
  Checkbox,
  Modal,
  AccountCard,
  AccountManager,
  SimpleCarousel,
} from "@deriv/ui";
import MdxLayout from "../mdx-layout";

type TMdxProvider = {
  children?: ReactNode;
  extra_components: {
    Localize: ({
      translate_text,
      i18n_default_text,
    }: {
      i18n_default_text?: string;
      translate_text?: string;
    }) => ReactElement;
  };
  platform: "deriv-com" | "deriv-app";
};

const components = {
  Button,
  Text,
  Checkbox,
  Modal,
  AccountCard,
  AccountManager,
  SimpleCarousel,
  wrapper: MdxLayout,
};

// eslint-disable-next-line no-empty-pattern
const DerivMdxProvider = ({
  children,
  extra_components,
  platform,
}: TMdxProvider) => {
  const { Localize } = extra_components;

  const InternalLocalize = ({ text }: { text?: string }) => {
    if (platform === "deriv-app") {
      return <Localize i18n_default_text={text} />;
    }
    return <Localize translate_text={text} />;
  };

  return (
    <MDXProvider
      components={{
        ...components,
        ...extra_components,
        Localize: InternalLocalize,
      }}
    >
      {children}
    </MDXProvider>
  );
};

export default DerivMdxProvider;
